@media only screen and (min-width: 768px) {
    .pageRegister {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100vw;
      height: 100vh;
      background-image: url("../../images/backgroundRegistration/regbg_tabl_@1x.webp");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
    }
  }

  @media only screen and (min-width: 768px) and (min-resolution: 192dpi) {
    .pageRegister {
      background-image: url("../../images/backgroundRegistration/regbg_tabl_@2x.webp");
    }
  }

  @media only screen and (min-width: 1280px) {
    .pageRegister {
      background-image: url("../../images/backgroundRegistration/regbg_desk_@1x.webp");
    }
  }

  @media only screen and (min-width: 1280px) and (min-resolution: 192dpi) {
    .pageRegister {
      background-image: url("../../images/backgroundRegistration/regbg_desk_@2x.webp");
    }
  }

